*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
  color: #e1e1e1;
  font-family: monospace;
}

.main-body{
  position: absolute;
  top:0;
  left:0;
  height: 100%;
  width: 100%;
  background-color: #111111;
}

.modal{
  font-size: 1.5rem;
  position: fixed;
  margin-top: 1rem;
  height: 2rem;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.body{
  position: absolute;
  top: 3rem;
  left: 1rem;
}

.terms{
  font-size: 1.3rem;
}

.terms strong{
  font-size: 1.4rem;
}

.form{
  font-size: 1.4rem;
  margin-top: 4rem;
}

.form label{
  font-size: 1.4rem;
}

input{
  background: none;
  border: #e1e1e1 solid 1px;
  border-radius: 0.3rem;
  font-size: 1.4rem;
  margin-left: 1rem;
  margin-top: 1rem;
  padding: 0.3rem;
}

input:focus{
  outline: none;
  background-color: #262626;
  color: #fff;
}

button{
  background: none;
  border: #e1e1e1 solid 1px;
  border-radius: 0.3rem;
  padding: 0.2rem;
  margin-top: 1rem;
  margin-left: 1rem;
}

.messages div{
  font-size: 1.4rem;
}